import React, { PureComponent } from 'react'

export default class InfoPage extends PureComponent {
	constructor(props) {
		super(props)
	
		this.state = {
			 releases:[]
		}
	}
	
	async componentDidMount(){
		let res = await fetch('https://api.github.com/repos/spoof14/araf');
		res = await res.json();
		console.log(res)
		this.setState({releases:res})
	}
	render() {
		return (
			<div>
				
			</div>
		)
	}
}
